    font-size: 1.8rem;
}

/**************** PARAGRAPH **************/

._2hquu p {
    font-size: 1.21rem;
}

/**************** TABLE *******************/

._2hquu table {
    border-spacing: 0;
    border-collapse: collapse;
}

._2hquu tr {
    background-color: #fff;
    border-top: 1px solid #c6cbd1;
}

._2hquu th, ._2hquu td {
    padding: 6px 13px;
    border: 1px solid #dfe2e5;
    font-weight: 500;
}

._2hquu td {
    font-weight: normal;
}

._2hquu tr:nth-child(even) {
    background-color: #f6f8fa;
}

/**************** BLOCKQUOTE *******************/

._2hquu blockquote {
    font-size: 1.22rem;
    padding: 0.5rem 1.5rem;
    border-left: 3px solid #3eb0ef;
    display: flex;
    align-items: center;
}

._2hquu blockquote p {
    margin: 0;
}

/**************** LIST *******************/

._2hquu ul, ._2hquu ol {
    font-size: 1.21rem;
}

/**************** IMAGES AND IFRAMES *******************/

._2hquu img, ._2hquu iframe {
    max-width: 100%;
}
